.main-slider{margin:0;overflow:visible}.slide{margin:0 auto;overflow-y:hidden;position:relative;display:block}.slide_1{background:#BDECB6;background-size:100% 100%}.slide_2{background:#fdfdfd}.slide_3{background:#CD9575}.box{width:800px;margin:0 auto;height:100vh;max-width:1000px;display:flex;flex-flow:row;justify-content:space-between;align-items:center;line-height:1.2}.box img{display:block}@media only screen and (max-width: 991px){.box{width:auto;max-width:800px}}@media only screen and (max-width: 540px){.box{flex-flow:column;justify-content:space-around}.box .box2{margin:0}}.slide_3{background:#FFDEAD}.box{position:relative}.box .name{font-size:40px;font-family:'Roboto', arial;transform:uppercase}
